import { ACTIONS } from "./constants";
import { v4 } from "uuid"

export const setMoment = text => ({ type: ACTIONS.SET_MOMENT, text })

export const setNote = text => ({ type: ACTIONS.SET_NOTE, text })

export const saveTask = (id, text, duration) => ({
    type: ACTIONS.SAVE_TASK, text, duration,
    id: id ? id : v4()
})

export const checkTask = id => ({ type: ACTIONS.CHECK_TASK, id })

export const editTask = id => ({ type: ACTIONS.EDIT_TASK, id })

export const deleteTask = id => ({ type: ACTIONS.DELETE_TASK, id })