import { ACTIONS } from "./constants"

export const moment = (state = {}, { type, ...payload }) => {
    switch (type) {
        case ACTIONS.SET_MOMENT:
            return { ...state, ...payload }
        default:
            return state
    }
}

export const note = (state = {}, { type, ...payload }) => {
    switch (type) {
        case ACTIONS.SET_NOTE:
            return { ...state, ...payload }
        default:
            return state
    }
}

export const tasklist = (state = [], { type, ...payload }) => {
    switch (type) {
        case ACTIONS.DELETE_TASK:
            return state.filter(item => item.id !== payload.id)
        case ACTIONS.CHECK_TASK:
        case ACTIONS.EDIT_TASK:
        case ACTIONS.SAVE_TASK:
            var action = { type, ...payload }
            if (state.map(i => i.id).includes(payload.id))
                return state
                    .map(item => item.id !== payload.id ?
                        item :
                        task(item, action))
                    .filter(item => !!item.text)
            else
                return [...state, task({}, action)]
        default:
            return state
    }
}

export const task = (state = {}, { type, ...payload }) => {
    switch (type) {
        case ACTIONS.SAVE_TASK:
            return { ...state, ...payload, editing: false }
        case ACTIONS.CHECK_TASK:
            return { ...state, done: !state.done }
        case ACTIONS.EDIT_TASK:
            return { ...state, editing: true }
        default:
            return state
    }
}
