import React from 'react'
import { store } from '../store/store'
import { Task } from './Task'
import { saveTask, checkTask, editTask, deleteTask } from '../store/actions'

export const List = () => {
    const { tasklist = [] } = store.getState()

    const onSave = (id, text, duration) => {
        store.dispatch(saveTask(id, text, duration))
    }
    const onCheck = id => store.dispatch(checkTask(id))
    const onEdit = id => store.dispatch(editTask(id))
    const onDelete = id => store.dispatch(deleteTask(id))

    return (
        <div>
            <h2>Nicht vergessen</h2>
            {tasklist.map((task, i) => (
                <Task
                    key={i}
                    {...task}
                    onCheck={onCheck}
                    onSave={onSave}
                    onEdit={onEdit} />
            ))}
            <hr />
            <Task editing={true} onSave={onSave} />
        </div>
    )
}
