import React from 'react'
import { store } from '../store/store'
import { setNote } from '../store/actions';
import './notes.css'

export const Notes = () => {
    let _input;
    const { text = "" } = store.getState().note

    const save = () => {
        store.dispatch(
            setNote(_input.innerText)
        )
    }
    return (
        <div>
            <h2>Notizen</h2>
            <div contentEditable
                ref={v => _input = v}
                onBlur={save}
                defaultValue={text}
                className="theme-l1 note w3-round w3-mobile w3-padding">
                {text}
            </div>
        </div>
    )
}