import React from 'react'
import './task.css'

export const Task = ({ id = "", text = "", done = false, duration, editing = false, onSave = f => f, onCheck = f => f, onEdit = f => f }) => {

    const save = () => onSave(id, _text.value, _duration.value)
    const check = () => onCheck(id)
    const edit = () => onEdit(id)

    let _text, _duration

    if (editing)
        return (
            <div className="w3-row">
                <input
                    defaultValue={text}
                    autoFocus
                    className="w3-col l7 m7 s12 w3-input"
                    placeholder="Neue Aufgabe"
                    type="text"
                    ref={v => _text = v}
                />
                <input
                    defaultValue={duration}
                    type="duration"
                    className="w3-col l2 m2 s12 w3-input" placeholder="Dauer"
                    style={{ margin: "0 0.5em" }}
                    ref={v => _duration = v}
                />
                <input
                    type="button"
                    value="Speichern"
                    className="w3-col l2 m2 s4 w3-border-bottom w3-button"
                    onClick={save}
                />
            </div>
        )

    return (
        <div className="w3-row w3-margin">
            <span
                style={{ textDecoration: done ? "line-through" : "none" }}
                className="text w3-col l9 m9 s9"
                onClick={edit}>
                {text}
            </span>
            <span
                style={{ textDecoration: done ? "line-through" : "none" }}
                className="duration w3-border-bottom w3-col l2 m2 s2 w3-center"
                onClick={edit}>
                {duration}
            </span>
            <input
                type="checkbox"
                defaultChecked={done}
                onChange={check}
                className="checkbox w3-col l1 m1 s1" />
        </div>
    )
}
